.services {
  background-color: var(--base-100);
  z-index: 20;
  margin-top: -100dvh;
  position: relative;
}

.services__container {
  padding-bottom: 20px;
}

.services__list {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.services__navbar {
  z-index: 15;
  background-color: var(--base-100);
  padding: 20px 0;
  position: sticky;
  top: 0;
}

.services__navbar-inner {
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  display: flex;
}

.services__navs {
  align-items: center;
  gap: 8px;
  display: flex;
}

.services__nav.button {
  -webkit-backdrop-filter: unset;
  box-shadow: none;
  text-transform: none;
  text-wrap: nowrap;
  white-space: nowrap;
  border: 1px solid #0000;
  border-radius: 20px;
  outline: none;
  min-width: 168px;
  height: 50px;
  padding: 20px 28px;
  font-size: 14px;
  font-weight: 400;
}

.services__nav.active {
  background-color: #ff5a181a;
  border: 1px solid #ff5a1866;
}

.card__footer-description--small {
  font-family: var(--font-family-first);
  color: var(--base-content-100);
  width: 100%;
  max-width: 296px;
  font-size: 16px;
  font-weight: 400;
  line-height: 120%;
}

@media (width <= 1024px) {
  .services__navbar-inner {
    flex-direction: column;
    justify-content: start;
    gap: 20px;
  }
}

@media (width <= 768px) {
  .services__container {
    padding-bottom: 10px;
  }

  .services__navbar-inner {
    gap: 26px;
  }

  .services__navs {
    scrollbar-width: none;
    -ms-overflow-style: none;
    width: 100%;
    overflow-x: auto;
  }

  .services__navs::-webkit-scrollbar {
    display: none;
  }

  .services__nav.button {
    font-size: 14px;
    line-height: 1;
  }

  .services__navbar-inner {
    align-items: start;
  }
}

@media (width >= 1024px) and (height <= 768px) {
  .services__nav.button {
    padding: 16px;
  }
}

@media (width >= 768px) and (width <= 1024px) {
  .services__navbar {
    padding-top: 20px;
  }
}
